@media screen and (min-width: 768px) {
  .homeGalleryMuiNe {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(4, 8vh);
    grid-gap: 0.5rem;
  }

  .caption {
    position: relative;
    border-top: 0px;
    background: rgba(1, 1, 1, 0.1);
    min-height: 10px;
    padding: 8px 25px;
  }
  .homeGalleryMuiNe__item {
    margin: 0;
  }

  .homeGalleryMuiNe__img {
    width: 100%;
    height: calc(100% - 40px);
    object-fit: cover;
    display: block;
  }

  .homeGalleryMuiNe__item--1 {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .homeGalleryMuiNe__item--2 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .homeGalleryMuiNe__item--3 {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 10;
  }

  .homeGalleryMuiNe__item--4 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 4;
    grid-row-end: 7;
  }

  .homeGalleryMuiNe__item--5 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 7;
    grid-row-end: 10;
  }
}

@media screen and (min-width: 1366px) {
  .homeGalleryMuiNe {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(8, 8vh);
    grid-gap: 0.5rem;
  }

  .caption {
    position: relative;
    border-top: 0px;
    background: rgba(1, 1, 1, 0.1);
    min-height: 10px;
    padding: 8px 25px;
  }
  .homeGalleryMuiNe__item {
    margin: 0;
  }

  .homeGalleryMuiNe__img {
    width: 100%;
    height: calc(100% - 40px);
    object-fit: cover;
    display: block;
  }

  .homeGalleryMuiNe__item--1 {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 5;
  }

  .homeGalleryMuiNe__item--2 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 5;
  }

  .homeGalleryMuiNe__item--3 {
    grid-column-start: 7;
    grid-column-end: 10;
    grid-row-start: 1;
    grid-row-end: 9;
  }

  .homeGalleryMuiNe__item--4 {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 5;
    grid-row-end: 9;
  }

  .homeGalleryMuiNe__item--5 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 5;
    grid-row-end: 9;
  }
}
