/*=========================================================
 Room Grid Css
=========================================================*/

.room-grid {
  border: 1px solid #ebebeb;
  overflow: hidden;
  border-radius: 5px;
}
.room-grid .room-top-wrap {
  position: relative;
}
.room-grid .room-top-wrap .muine-modal-button {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 0;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  padding: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  border-radius: 5px;
}
.room-grid:hover .room-top-wrap .muine-modal-button {
  opacity: 1;
  filter: alpha(opacity=100);
}
.room-grid .room-main-wrap {
  padding: 20px 30px 10px;
